    <!-- 保障详情页面 -->
<template>
  <div>
    <van-popup
      v-model="show"
      class="popup"
      position="top"
      :style="{ height: '100%' }"
    >
      <div class="wrap_content">
        <div class="top_wrap">
          <h3>保障责任</h3>
          <div class="look_top">
            <div class="top_left">保障期间</div>
            <div>{{ insurePeriod }}</div>
          </div>
          <div class="look_top">
            <div class="top_left">投保年龄</div>
            <div>{{ min_max }}</div>
          </div>

          <div class="look_top">
            <div class="top_left">保单生效日</div>
            <div>{{ starttime }}</div>
          </div>
          <div class="look_top">
            <div class="top_left">保单终止日</div>
            <div>{{ timeEnd }}</div>
          </div>
        </div>
        <div v-for="(title, index) in prdro.prdInsuranceDutyList" :key="index">
          <div class="top_wrap">
            <div class="look_top">
              <div class="top_left">
                <div>{{ title.ilName }}</div>
                <div>{{ title.ilCode }}</div>
              </div>
              <div>{{ title.insuredAmount }}元</div>
            </div>
            <div class="looktop_text">
              {{ title.dutyDetails }}
            </div>
          </div>
        </div>
      </div>
      <van-button
        class="btn_bottom"
        round
        block
        type="info"
        @click="back"
        native-type="submit"
        >返回</van-button
      >
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},

  data() {
    return {
      show: false,
      prdro: "",
      min_max: "",
      tieme: "",
      // timeEnd:''
    };
  },

  created() {    this.prdro = JSON.parse(sessionStorage.getItem("productData"));
    this.tieme = JSON.parse(sessionStorage.getItem("planPage"));},
  mounted() {

    this.min_max = sessionStorage.getItem("minMax");

    console.log(this.min_max, "年龄");
    console.log(this.prdro, "prdro");
  },
  methods: {
    back() {
      this.show = false;
    },
  },
  watch: {},
  computed: {
    insurePeriod() {
      const plan =  JSON.parse(sessionStorage.getItem("productData")).plans[0]; //至少一条保障
      let insurePeriod = "";
      switch (plan.insuYearFlag) {
        case "Y":
          insurePeriod = plan.insuYear + "年";
          break;
        case "M":
          insurePeriod = plan.insuYear + "月";
          break;
        case "D":
          insurePeriod = plan.insuYear + "天";
          break;
        case "A":
          insurePeriod =
            "至被保险人年满" + plan.insuYear + "周岁后首个保单周年日";
          break;
        default:
          insurePeriod = "";
          break;
      }
      return insurePeriod;
    },
    starttime() {
      let abc = this.tieme.time;
      let startime =
        `${abc.slice(0, 4)}` + `-${abc.slice(4, 6)}` + `-${abc.slice(6, 8)}`;
      return startime;
    },
    timeEnd() {
      const end = this.tieme.time;
      const endtime =
        `${Number(end.slice(0, 4)) + 1}` +
        `-${end.slice(4, 6)}` +
        `-${end.slice(6, 8)}`;
      return endtime;
    },
  },
};
</script>

<style lang='less' scoped>
.contont {
  line-height: 4vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90%;
  margin-bottom: 10px;
}
.matter {
  flex: 1;
  border: 1px #ccc solid;
  padding-top: 3vh;
  width: 95%;
}
.matter_top {
  // position:fixed;
  // top:0;
  background: #fff;
  width: 95%;
  border: solid 1px #eee;
  padding-top: 5%;
}
.matter:nth-child(1) {
  margin-bottom: 10px;
}

.van-row--justify-space-between {
  margin: 10px;
}
.btn_bottom {
  position: fixed;
  bottom: 0;
}
.wrap_content {
  padding-bottom: 10%;
  .top_wrap {
    background: #fff;
    border: 1px solid #fff;
    width: 95%;
    margin: 5% auto;
    .look_top {
      display: flex;
      text-align: left;
      justify-content: space-between;
      margin: 5% auto;
      width: 80%;

      .top_left {
        font-size: 16px;
        font-weight: 600;
        div:nth-child(2) {
          font-size: 15px;
          font-weight: normal;
        }
        div:nth-child(2) {
          margin-top: 10%;
        }
      }
    }
    .looktop_text {
      margin-bottom: 2%;
    }
  }
}

.popup {
  background: #edeef1;
}
</style>
