<template>
  <div class="view-info">
    <!-- <van-button
      style="display:flex"
              size="small"
              @click.prevent="back()"
              type="info"
              > 返回上一页 </van-button
   >  
   -->
    <div class="top_back" 
              @click.prevent="back()"
    >
      <span>
        <van-icon name="arrow-left"></van-icon>
      </span>

      <span> 返回 </span>
    </div>
    <!-- <div>
      <img
        style="height: ; margin-bottom: 4%"
        class="back"
        @click="back()"
        src="../../assets/img/back.png"
        alt=""
      />
    </div> -->
    <van-image :src="batchSrc" width="100%" radius="10px" class="image">
      <van-loading
        slot="loading"
        type="spinner"
        size="40"
        vertical
      ></van-loading>
      <template v-slot:error>
        <van-image :src="src" width="100%" radius="10px" class="image">
          <van-loading
            slot="loading"
            type="spinner"
            size="40"
            vertical
          ></van-loading>
        </van-image>
      </template>
    </van-image>
    <!-- 保障详情 -->
    <div class="detail_box">
      <div class="title">
        <div style="font-weight: 700; width: 50vw">
          <van-col span="12">
            <h4>保障责任</h4>
          </van-col>
        </div>
        <div class="lookdetail">
          <h4 @click="lookDetailsPage()">查看详情</h4>
        </div>
      </div>

      <div class="detail" @click="showAll = !showAll">
        <div
          class="detail_insurance"
          :data-detail="'保障期间'"
          :data-money="insurePeriod"
        ></div>
        <div
          class="detail_insurance"
          :data-detail="'投保年龄'"
          :data-money="agePeriod"
        ></div>
        <div
          class="detail_insurance"
          :data-detail="'保单生效日'"
          :data-money="starttime"
        ></div>
        <div
          class="detail_insurance"
          :data-detail="'保单终止日'"
          :data-money="timeEnd"
        ></div>
        <div
          v-for="(item, i) in liability"
          :key="i"
          class="detail_insurance"
          :data-detail="item.ilName"
          :data-money="item.description"
        ></div>
      </div>
    </div>
    <div class="hulianwang"> 此产品仅支持互联网销售</div>  
    <div class="position-fixed">
      <div class="position-fixed-div">
        <div class="money_wrap">
          <!-- <van-icon name="phone-o" @click="callPhone(4008855668)" /> -->
          <img
            class="back"
            style="margin-left: 0%; height: 55%"
            @click="callPhone(4008855668)"
            src="../../assets/img/phone.png"
            alt=""
          />
          <div class="money_new">
            <div>优惠价 {{ productData.initPyFAmt }} 元</div>
            <div
              v-if="productData.initPyFAmt < productData.initSumAmt"
              style="text-decoration: line-through"
            >
              原价 {{ productData.initSumAmt }} 元
            </div>
          </div>
          <!-- <span class="money">{{productData.initPyFAmt}}</span>
          <span class="money">{{productData.initSumAmt}}</span> -->
          
        </div>
        <div class="insure" @click="immediatelyInsured">立即投保</div>
      </div>
    </div>

    <!--  您即将投保流程 -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '40%' }"
      :close-on-click-overlay="false"
    >
      <div class="top">
        <div class="img">
          <img src="../../assets/img/icon.png" alt="" />
          <h3>消费者权益保障提示</h3>
        </div>

        <p class="clause">
          您即将投保流程,请您认真阅读保险条款。为保障您的权益，您的投保操作流程将被记录，请知悉!
        </p>
      </div>
      <van-button class="button_info" type="info" @click="buttonInfo"
        >我知道了</van-button
      ></van-popup
    >
    <!-- 保障详情页面 -->
    <detailsPage ref="detailsPage"></detailsPage>
  </div>
</template>

<script>
import Vue from "vue";
import { Dialog } from "vant";
import { mapState, mapActions, mapGetters } from "vuex";
import detailsPage from "./components/detailsPage.vue";
import loading from "@/components/loading/index.js";
import skeleton from "@/components/skeleton";
export default {
  name: "",
  components: { detailsPage },

  data() {
    return {
      show: true,
      tieme: "",
    };
  },
  computed: {
    // 保单生效日  失效日
    starttime() {
      let abc = this.tieme.time;

      let startime =
        `${abc.slice(0, 4)}` + `-${abc.slice(4, 6)}` + `-${abc.slice(6, 8)}`;
      return startime;
    },
    timeEnd() {
      const end = this.tieme.time;
      const endtime =
        `${Number(end.slice(0, 4)) + 1}` +
        `-${end.slice(4, 6)}` +
        `-${end.slice(6, 8)}`;
      return endtime;
    },
    ...mapState({
      batchNum: (state) => state.app.batchNo,
      batchInfo: (state) => state.app.batchInfo,
      batch: (state) => state.app.batch,
    }),
    ...mapGetters({
      productData: "product",
    }),
    src() {
      return this.$isPrd
        ? `${this.$baseUrl}statics/cardTemp/${this.productData.prd_id}.jpg`
        : this.productData.batchNo == "001"
        ? require("@/assets/image/qinwuyuan1.png")
        : require("@/assets/image/qinwuyuan2.png");
    },
    batchSrc() {
      return `${this.$baseUrl}statics/cardTemp/${
        this.batchInfo.batchNo || this.productData.batchNo
      }.jpg`;
    },
    agePeriod() {
      let min = this.productData.min_app_age
          ? this.productData.min_app_age
          : "出生满30天",
        max = this.productData.max_app_age
          ? this.productData.max_app_age + "周岁"
          : "出生满30天";
          sessionStorage.setItem("minMax",`${min}-${max}`);
      return `${min}-${max}`;
    },
    insurePeriod() {
      const plan = this.productData.plans[0]; //至少一条保障
      let insurePeriod = "";
      switch (plan.insuYearFlag) {
        case "Y":
          insurePeriod = plan.insuYear + "年";
          break;
        case "M":
          insurePeriod = plan.insuYear + "月";
          break;
        case "D":
          insurePeriod = plan.insuYear + "天";
          break;
        case "A":
          insurePeriod =
            "至被保险人年满" + plan.insuYear + "周岁后首个保单周年日";
          break;
        default:
          insurePeriod = "";
          break;
      }
      return insurePeriod;
    },
    liability() {
      return this.productData.prdInsuranceDutyList;
      // .slice(
      //   0,
      //   this.showAll ? 999 : 2
      // );
    },
  },
  created() {
    this.tieme = JSON.parse(sessionStorage.getItem("planPage"));

    skeleton.hide();
    console.log(this.productData, "productData");
    sessionStorage.setItem("productData", JSON.stringify(this.productData));
  },
  mounted() {
    // this.prdjson();

    // 调可回溯先调这个

    document.title = this.productData.prd_name;
    this.$api.card
      .cardSwitch({}, true)
      .then(
        (res) =>
          res.screencapType.includes(
            JSON.parse(sessionStorage.getItem("productData")).cardSaleType
          ) && this.getScreenCap(res.url, res.type)
      );
  },
  methods: {
    back() {
      this.$router.back();
    },
    // 好像是可回溯的接口

    getScreenCap(url = "yeefxclick/yeefximg.js", type = "10000004") {
      var param = {
        prodId: JSON.parse(sessionStorage.getItem("productData")).prd_id, //产品id
        time: +new Date(),
      };
      console.log("param", param);
      this.$api.common.generateTraceId(param, true).then((res) => {
        if (res.status && res.status == 1 && res.data.traceFlag === "1") {
          // import("http://10.100.18.146/yeefxclick/count/js/yeefximg.js")
          if (res.data) {
            window._yfx_website = type || "10000004"; //可根据提供的文档替换
            //是否开启截图
            window._yfx_isopenimg = "auto"; //auto:dom元素记录和截图并存，dom:元素记录模式，img:截图模式，默认采用auto录屏模式
            //本次视频回溯的唯一标志会话id, 默认回溯系统会自动生成一个，也可以根据业务逻辑自行指定一个初始值；
            //如果要避免一直未提交投保单产生大量的数据，可以在每次进入投保首页的时候重新指定一个新的会话id（测试环境下推荐此方式）；
            //在指定的情况下，需要确保本值被优先初始化，然后调用后续的js代码，如果存在异步加载情况，优先使用方法_yfx_set_session('会话id');
            window._yfx_setimgsession = res.data.traceId;
            window._yfx_imgversion = res.data.verNo; //平台页面版本号
            window._yfx_imggoodsid = JSON.parse(
              sessionStorage.getItem("productData")
            ).prdId; //保险产品id
            window._yfx_imggoodsname = JSON.parse(
              sessionStorage.getItem("productData")
            ).prd_name; //保险产品名称
            window._yfx_imgpagenum = "1"; //页面节点编号，如第1个页面，可写1
            window._yfx_imgpagename = "学平险页面"; //页面节点名称，如保险产品详情，可写保险介绍页面

            //开启调试，选填项，默认false关闭，true开启
            window._yfx_isdebug = false;
            let scriptDom = document.createElement("script");
            scriptDom.charset = "utf-8";
            scriptDom.src =
              location.origin +
              location.pathname.replace("/elife-card", "") +
              url;
            let scriptDoms = document.getElementsByTagName("script")[0];
            scriptDoms.parentNode.insertBefore(scriptDom, scriptDoms);
          }
        }
      });
    },
    // 点击查看详情
    lookDetailsPage() {
      this.$refs.detailsPage.show = true;
    },
    // 调用拨号功能
    callPhone(phoneNumber) {
      Dialog.confirm({
        title: "",
        message: "是否拨打客服热线？",
      })
        .then(() => {
          window.location.href = "tel:" + phoneNumber;
        })
        .catch(() => {});
    },
    buttonInfo() {
      this.show = false;
    },
    // 点击立即投保的页面
    immediatelyInsured() {
      this.$router.push({
        // path: "/healthInform",
        path: "/insuredObject",
      });
    },
    cardActiveResultCycilc(flowNo) {
      const time = 0;
      if (time > 20) return;
      return this.$api.card.cardActiveResult({ flowNo }).then((res) => {
        console.log(res.data, "batch");
        if (res.status == 1) {
          if (res.data) {
            console.log(
              this.batch.batchNo,
              res.data.batchNo,
              /KDPC/.test(this.batch.batchNo),
              "this.batch.batchNo != res.data.batchNo && /KDPC/.test(this.batch.batchNo"
            );
            if (
              this.batch.batchNo != res.data.batchNo &&
              /KDPC/.test(this.batch.batchNo)
            ) {
              router.replace({
                name: "insuranceInformation",
                params: {
                  batchNo: res.data.batchNo,
                },
                query: state.routeQuery,
              });
              return;
            }
            this.$store.commit("SET_IMPORT_FLAG", res.data.importActiveFlag);
            let batchFlagDate = {};
            this.$api.static.batchFlag({}, true).then((resJson) => {
              loading.hide();
              for (let s = 0; s < resJson.batchFlagDate.length; s++) {
                if (res.data.batchNo == resJson.batchFlagDate[s].batch) {
                  batchFlagDate = { tip: resJson.batchFlagDate[s].tip };
                  break;
                }
              }
              const channelCodeOld = sessionStorage.getItem("channelCode");
              const prdId = res.data.prdId;
              // let [productData, materialList, exonerationNoticeCardRisk] = Promise.all([
              //     this.$api.static.product({url: `/${channelCodeOld}/${prdId}/${prdId}.json`, params: {prdId}}, true),
              //     this.$api.static.product({url: `/${channelCodeOld}/${prdId}/${prdId}MaterialList.json`}, true),
              //     this.$api.static.exonerationNoticeCardRisk({}, true)
              // ])
              switch (res.data.plchdRespCode) {
                case "0": // 激活失败
                  // skeleton.hide();
                  this.$router.replace({
                    name: "card-result",
                    params: {
                      type: "0",
                      message: res.data.plchdRespDesc,
                    },
                  });
                  break;
                case "1": // 激活成功
                  this.$store.commit("SET_BATCH_INFO", res.data);
                  this.$store.dispatch("getProductResult", {
                    prdId: res.data.prdId,
                    isShow: batchFlagDate.tip ? batchFlagDate : true,
                  });
                  const productData = this.$api.static
                    .product(
                      {
                        url: `/${channelCodeOld}/${prdId}/${prdId}.json`,
                        params: { prdId },
                      },
                      true
                    )
                    .then((res) => {
                      this.$store.commit("SET_PRODUCT_DATA", res.data);

                      const prod = JSON.stringify(this.productData);
                      sessionStorage.setItem("productData", prod);
                    });
                  break;
                case "2": // 白名单激活，跳往白名单页面
                  let isShow = batchFlagDate.tip ? batchFlagDate : true;
                  showDialog(isShow, () => {
                    // skeleton.hide();
                    this.$store.commit("SER_CHECK_FLAG", false);
                    router.replace({
                      name: "info-check",
                      params: state.routeParams,
                    });
                  });
                  break;
              }
            });
          } else {
            setTimeout(() => {
              // this.$store.dispatch("cardActiveResult", flowNo)
              this.cardActiveResultCycilc(flowNo);
            }, 3000);
          }
        }
      });
    },
    prdjson() {
      let xpChannel = sessionStorage.getItem("channelCode");
      let xpPC = "KDPC115220220615001";
      const parms = {
        batchNo: xpPC,
        channelCode: xpChannel,
      };
      this.$api.card.activationApply(parms).then((res) => {
        if (res.status == 1) {
          console.log(res.data, "res.data");
          let insuranceInformation = {
            plchdCardNo: res.data,
          };

          sessionStorage.setItem(
            "insuranceInformation_PAGE",
            JSON.stringify(insuranceInformation)
          );
          this.cardActiveResultCycilc(res.data);
        }
      });
    },
  },
  watch: {},
};
</script>

<style lang='less' scoped>
.button_info {
  position: fixed;
  bottom: 0;
  width: 100%;
  border-radius: 18px;
  left: 0;
}
.detail_box {
  .title {
    display: flex;
    justify-content: space-between;
    .lookdetail {
      font-size: 14px;
      color: #1989fa;
    }
  }
}
.top {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.img {
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    margin-right: 10px;
    height: 30px;
  }
}
.clause {
  margin: 10px;
  line-height: 30px;
  text-indent: 1cm;
  text-align: left;
}

.product {
  padding-bottom: 50px;

  .product-img {
    width: 100%;
    height: 50vh;
  }
}

.product-information {
  margin: 10px;
  h4 {
    margin: 10px;
  }
}

.van-row--justify-space-between {
  // margin: 10px;
}
.position-fixed {
  position: fixed;
  bottom: 0;
  left: 0;
  // height: 50px;
  background: #fff;
  width: 100%;
  .position-fixed-div {
    display: flex;
    .money_wrap{
      display:flex;
      width:50%;
      justify-content: center;
      align-items: center;
      color: orange;
      #icon {
        font-size: 16px;
      }
      .money_new {
        margin: 0 0 0 10%;
      }
    }
    .insure{
      background: #1989fa;
      color:#fff;
      height: 50px;
      width:50%;
      line-height: 50px;
    }
  }
}
.top_back {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20vw;
  height: 8vw;
  // background: #1989fa;
  border-radius: 13px;
  border: #1989fa 1px solid;
  margin-bottom: 2vw;
  color: #1989fa;
  span :nth-child(1) {
    margin-top: 1.5vw;
    margin-right: 2vw;
  }
  span :nth-child(2) {
    margin-left: 2vw;
  }
}
.hulianwang{
  margin-top:10vw;
  color: #d2d4d6;
  font-size: 14px;
}
</style>
<style src='./index.less' lang="less" scoped></style>
